import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';

function Unidades() {
  var html = 'https://scope-server.herokuapp.com';

  // recuperando estados.
  const changeState = useDispatch();
  const view = useSelector((state) => state.unidades);
  const idusuario = useSelector((state) => state.idusuario);
  const idhospital = useSelector((state) => state.idhospital);

  // carregamento do componente.
  useEffect(() => {
    if (view === 1) {
      // carregando as listas de hospitais.
      loadUsuariosVersusHospitaisVersusUnidades();
      loadUnidades();
    }
    // eslint-disable-next-line
  }, [view]);

  // carregando hospitais vinculados ao usuário.
  const [usuariosxhospitaisxunidades, setusuariosxhospitaisxunidades] = useState([]);
  const loadUsuariosVersusHospitaisVersusUnidades = () => {
    axios.get(html + '/usuariosxhospitaisxunidades').then((response) => {
      setusuariosxhospitaisxunidades(response.data);
    });
  }

  // carregando a lista de hospitais (para captura do nome do hospital).
  const [unidades, setunidades] = useState([]);
  const loadUnidades = () => {
    axios.get(html + '/unidades').then((response) => {
      setunidades(response.data);
    });
  }

  // selecionando um hospital e renderizando o componente "unidades".
  const selectUnidade = (item) => {
    changeState({
      type: 'CALENDÁRIO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadIdhospital: idhospital,
      payloadIdunidade: item.idunidade,
    });
  }

  // exibindo a lista de hospitais vinculados ao usuário.
  function ListUnidades() {
    return (
      <div
        className="scroll"
        id="LISTA DE HOSPITAIS"
        style={{
          height: 0.5 * window.innerHeight,
          width: window.innerWidth > 800 ? 0.5 * window.innerWidth : 0.8 * window.innerWidth,
          flexWrap: 'wrap',
        }}
      >
        {usuariosxhospitaisxunidades.filter(item => item.idusuario == idusuario).map((item) =>
          <button
            className='blue-button'
            onClick={() => selectUnidade(item)}
            style={{
              width: '100%',
            }}
          >
            {unidades.filter(value => value.id == item.idunidade).map(value => value.unidade)}
          </button>
        )}
      </div>
    );
  }
  // renderização do componente.
  if (view === 1) {
    return (
      <div className="main fade-in">
        <ListUnidades></ListUnidades>
      </div >
    );
  } else {
    return null;
  }
}
export default Unidades;
