import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

// importações do redux.
import { createStore } from 'redux';
import { Provider } from 'react-redux';

// definindo o estado inicial.
const initialState = {
  // telas.
  login: 1,
  hospitais: 0,
  unidades: 0,
  calendario: 0,
  // payloads.
  idusuario: 0,
  idhospital: 0,
  idunidade: 0,
}

// definindo reducer.
const reducerGscope = function reducerGscope(state = initialState, action) {
  switch (action.type) {
    // tela inicial.
    case 'LOGIN_ON':
      return {
        // telas.
        login: 1,
        hospitais: 0,
        unidades: 0,
        calendario: 0,
      };
    case 'HOSPITAIS_ON':
      return {
        // telas.
        login: 0,
        hospitais: 1,
        unidades: 0,
        calendario: 0,
        // payloads:
        idusuario: action.payloadIdusuario,
      };
    case 'UNIDADES_ON':
      return {
        // telas.
        login: 0,
        hospitais: 0,
        unidades: 1,
        calendario: 0,
        // payloads:
        idusuario: action.payloadIdusuario,
        idhospital: action.payloadIdhospital,
      };
    case 'CALENDÁRIO_ON':
      return {
        // telas.
        login: 0,
        hospitais: 0,
        unidades: 0,
        calendario: 1,
        // payloads:
        idusuario: action.payloadIdusuario,
        idhospital: action.payloadIdhospital,
        idunidade: action.payloadIdunidade,
      };
    default:
      return state;
  }
};

// criando a store.
const store = createStore(
  reducerGscope,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
