import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';

function Hospitais() {
  var html = 'https://scope-server.herokuapp.com';

  // recuperando estados.
  const changeState = useDispatch();
  const view = useSelector((state) => state.hospitais);
  const idusuario = useSelector((state) => state.idusuario);

  // carregamento do componente.
  useEffect(() => {
    if (view === 1) {
      // carregando as listas de hospitais.
      loadUsuariosVersusHospitais();
      loadHospitais();
    }
    // eslint-disable-next-line
  }, [view]);

  // carregando hospitais vinculados ao usuário.
  const [usuariosxhospitais, setusuariosxhospitais] = useState([]);
  const loadUsuariosVersusHospitais = () => {
    axios.get(html + '/usuariosxhospitais').then((response) => {
      setusuariosxhospitais(response.data);
    });
  }

  // carregando a lista de hospitais (para captura do nome do hospital).
  const [hospitais, sethospitais] = useState([]);
  const loadHospitais = () => {
    axios.get(html + '/hospitais').then((response) => {
      sethospitais(response.data);
    });
  }

  // selecionando um hospital e renderizando o componente "unidades".
  const selectHospital = (item) => {
    changeState({
      type: 'UNIDADES_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadIdhospital: item.idhospital,
    });
  }

  // exibindo a lista de hospitais vinculados ao usuário.
  function ListHospitais() {
    return (
      <div
        className="scroll"
        id="LISTA DE HOSPITAIS"
        style={{
          height: 0.5 * window.innerHeight,
          width: window.innerWidth > 800 ? 0.5 * window.innerWidth : 0.8 * window.innerWidth,
          flexWrap: 'wrap',
        }}
      >
        {usuariosxhospitais.filter(item => item.idusuario == idusuario).map((item) =>
          <button
            className='blue-button'
            onClick={() => selectHospital(item)}
            style={{
              width: '100%',
            }}
          >
            {hospitais.filter(value => value.id == item.idhospital).map(value => value.hospital)}
          </button>
        )}
      </div>
    );
  }
  // renderização do componente.
  if (view === 1) {
    return (
      <div className="main fade-in">
        <ListHospitais></ListHospitais>
      </div >
    );
  } else {
    return null;
  }
}
export default Hospitais;
