import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.svg';
import '../design.css';
import Toast from './Toast';

function Login() {
  var html = 'https://scope-server.herokuapp.com';

  // recuperando estados.
  const changeState = useDispatch();
  const view = useSelector((state) => state.login);

  // carregamento do componente.
  useEffect(() => {
    if (view === 1) {
      // limpando os campos de login e senha.
      document.getElementById('inputUsuario').value = '';
      document.getElementById('inputSenha').value = '';
    }
  }, [view]);

  // efetuando o login e recuperando a id do usuário.
  const loadUsuario = () => {
    var usuario = document.getElementById('inputUsuario').value;
    var senha = document.getElementById('inputSenha').value;
    var obj = {
      usuario: usuario,
      senha: senha,
    };
    axios.post(html + "/login/'" + usuario + "'/'" + senha + "'", obj).then((response) => {
      var x = [0, 1];
      x = response.data;
      if (x.length > 0) {
        var idusuario = x.map(item => item.id);
        toast('LOGIN EFETUADO COM SUCESSO', '#52be80', 3000);
        setTimeout(() => {
          changeState({
            type: 'HOSPITAIS_ON',
            // payloads.
            payloadIdusuario: idusuario,
          });
        }, 3500);
      } else {
        document.getElementById('inputUsuario').value = '';
        document.getElementById('inputSenha').value = '';
        toast('DADOS INCORRETOS', '#ec7063', 3000);
      }
    });
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // renderização do componente.
  if (view === 1) {
    return (
      <div className="main fade-in">
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <img alt="" src={logo} style={{ height: '25%' }}></img>
        <label className="title" style={{ margin: 10, fontSize: 28 }}>G-SCALE</label>
        <div className="default">
          <input
            autoComplete="off"
            placeholder="USUÁRIO"
            className="input"
            type="text"
            id="inputUsuario"
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'USUÁRIO')}
            style={{
              marginTop: 20,
              marginBottom: 10,
              width: 200,
              height: 50,
            }}
          ></input>
          <input
            autoComplete="off"
            placeholder="SENHA"
            className="input"
            type="password"
            id="inputSenha"
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'SENHA')}
            style={{
              margin: 0,
              marginBottom: 7.5,
              width: 200,
              height: 50,
            }}
          ></input>
          <button
            className="blue-button"
            style={{
              width: 205,
              color: '#ffffff',
              alignSelf: 'center',
            }}
            onClick={() => loadUsuario()}
          >
            ENTRAR
          </button>
        </div>
      </div >
    );
  } else {
    return null;
  }
}
export default Login;
