import React, { useState, useEffect } from 'react';

function Toast({ valor, cor, mensagem }) {
  const [viewtoast, setviewtoast] = useState(0);
  useEffect(() => {
    setviewtoast(valor);
  }, [valor])

  if (viewtoast === 1) {
    return (
      <div className="toast popup-bottom">
        <div
          className="default"
          style={{
            textAlign: 'center',
            backgroundColor: cor,
            padding: 25,
            minHeight: 50,
            maxHeight: 300,
            minWidth: 100,
            maxWidth: 300,
            color: '#ffffff',
            fontWeight: 'bold',
          }}>
          {mensagem}
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default Toast;
